"use-client"
import React, { useEffect, useRef } from 'react'
import Link from 'next/link'
import Image from 'next/image'
import { Button } from './ui/button'  // Make sure to import Button

const HeroSection = () => {
  const imageRef = useRef(null)

  useEffect(() => {
    const handleScroll = () => {
      const imageElement = imageRef.current
      if (!imageElement) return

      const scrollPosition = window.scrollY
      const scrollThreshold = 100

      if (scrollPosition > scrollThreshold) {
        imageElement.classList.add('scrolled')
      } else {
        imageElement.classList.remove('scrolled')
      }
    }

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  return (
    <section className='w-full pt-36 md:pt-48 pb-10'>
      <div className='space-y-6 text-center'>
        <div className='space-y-6 mx-auto'>
          <h1 className='gradient-title text-5xl font-bold md:text-6xl lg:text-7xl xl:text-8xl'>
            sjiffajifwe
            <br />
            selkfewfuhwefb
          </h1>
          <p className='mx-auto max-w-[600px] text-muted-foreground md:text-xl'>
            wehufbeqwfbaeuhfberqhfbqehfbehfbqewhuefbewqhufbewhfbe
            ewfewfewf ef
          </p>
        </div>

        <div className='flex justify-center gap-4'>
          <Link href='/dashboard'>
            <Button size='lg' className='px-8'>
              Lets go
            </Button>
          </Link>
          <Link href='https://www.google.com' target="_blank">
            <Button size='lg' className='px-8' variant='outline'>
              Lets go
            </Button>
          </Link>
        </div>

        <div className='hero-image-wrapper mt-5 md:mt-0'>
          <div ref={imageRef}>
            <Image
              src='/banner.jpeg'
              width={1280}
              height={729}
              alt='banner'
              className='rounded-lg shadow-2xl border mx-auto hero-image'
              priority
            />
          </div>
        </div>
      </div>
    </section>
  )
}

export default HeroSection
